.FollowersCard{
    width: 100%;
    border-radius: 0.7rem;
    gap: 1rem;
    display: flex;
    flex-direction: column;
    font-size: 13px;
}

.Follower{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Follower > div{
    display: flex;
    gap: 10px;
}

.FollowerImage{
    width: 3.2rem;
    height: 3.2rem;
    border-radius: 50%;
}

.FollowerName{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.FollowerName > span:nth-of-type(1){
    font-weight: bold;
}

.fc-button{
    height: 2rem;
    padding-left: 20px;
    padding-right: 20px;
}

.UnfollowButton{
    color: var(--orange);
    border: 2px solid var(--orange);
    cursor: pointer;
    background: transparent;
}