.Auth{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    gap: 4rem;
    position: relative;
}

.Auth > div{
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Left Side */
.AuthLeft{
    gap: 2rem;
}

.AuthLeft > img{
    width: 4rem;
    height: 4rem;
}

.WebName > h1{
    font-size: 3rem;
    background-color: red;

    /* Create the gradient */
    background-image: var(--buttonBg);

    /* Set the background size and repeat properties */
    background-size: 100%;
    background-repeat: repeat;

    /* Use the text as as mask for the background */
    /* This will show the gradient as a text color rather than element bg */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
}

.WebName > h6{
    font-size: 0.85rem;
}

.InfoForm{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}

.InfoInput{
    border: none;
    outline: none;
    background-color: var(--inputColor);
    border-radius: 8px;
    padding: 20px;
    flex: 1;
}

.InfoForm > div{
    display: flex;
    gap: 1rem;
    height: 2rem;
    width: 100%;
}

.InfoButton{
    width: 6rem;
    height: 2rem;
    align-self: flex-end;
}

.AuthForm{
    background-color: var(--cardColor);
    border-radius: 1rem;
    padding: 1rem;
}

.SwitchAuth{
    align-items: center;
    justify-content: center;
}

@media (max-width: 420px){
    .Auth{
        flex-direction: column;
    }

    .InfoInput{
        width: 20px;
    }
}